import { Goods } from './Goods/Goods';
import Menu from './Tuner/components/Menu/Menu';
import { MetaData } from 'components';

export default function Shop() {
  return (
    <MetaData
      title={`FightGym, sporting goods store`}
      disc={`FightGym, sporting goods store. Discount on new proteins`}
    >
      <Menu name='shop' />
      <div className='indentstart'></div>
      <Goods />
    </MetaData>
  );
}
